.BLE {
  position: relative;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  user-select: none;

}
.BLE__connectButton {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: blue;
  user-select: none;
  width: 30vh;
  height: 30vh;
  border-radius: 50%;
  transform: scale(1);
  animation: pulse 1s infinite;
}
@keyframes pulse {
  0% {
    transform: scale(0.98);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }

  100% {
    transform: scale(0.98);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}
.BLE__icon {
  user-select: none;
  width: 80%;
  height: auto;
  fill: white;
}
.BLE__logo {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url('../../logo2.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 30vh;
}

.BLE__title {
  position: absolute;
  width: 100%;
  bottom: 15%;
  font-size: 3vh;
  font-weight: bold;
}
.BLE__message {
  position: absolute;
  width: 100%;
  top: 15%;
  font-weight: bold;
}
