.TabBar {
  display: flex;
  width: 100%;
  justify-content: space-around;
  padding-top: 10px;
  padding-bottom: 10px;
}

.TabBar__tab {
  flex: 1 100%;
  padding: 0px;
  user-select: none;
  text-align: center;
  font-weight: bold;
  font-size: 2lvh;
  border-radius: 20px;
  position: relative;
}

.TabBar__button {
  position: relative;
  display: inline-block;
  padding: 15px;
  cursor: pointer;
}

.TabBar__flashHelper {
  position: absolute;
  background-color: white;
  width: 100%;
  height: 100%;
  opacity: 0;
  top: 0;
  right: 0;
  border-radius: 20px;
}

.TabBar__button__active {
  text-decoration: underline;
}

@keyframes flash1 {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes flash2 {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
