.TimeDisplay {
  position: relative;
  background-color: black;
  color: rgb(50, 255, 255);
  margin: 0;
  padding: 0;
}
.TimeDisplay__inactive {
  opacity: 0.3;
}

.TimeDisplay__time {
  font-size: 10lvh;
  text-align: center;
}
.TimeDisplay__mode {
  width: 90%;
  position: absolute;
  bottom: 0;
  font-size: 2.5lvh;
  /* background-color: blue; */
  text-align: right;
  padding-bottom: 10px;
}
