.DialogModal__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(126, 126, 126, 0.4);
}

.DialogModal__dialogContainer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-y: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.DialogModal__dialogContent {
  padding: 1rem;
  text-align: center;
  display: flex;
  justify-content: center;
  width: 90%;
}

.DialogModal__dialog {
  position: relative;
  user-select: none;
  width: 100%;
  overflow: hidden;
  border-radius: 1rem;
  background-color: black;
  padding: 1.5rem;
  vertical-align: middle;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  transition: all 0.3s ease;
}
.DialogModal__defaultTime {
  font-size: 1em;
  padding-bottom: 20px;
}
.DialogModal__buttonContainer {
  display: flex;
  /* justify-content: right; */
  justify-content: space-between;
  /* padding-right: 20px; */
  /* background-color: red; */
}

.DialogModal__buttonSubContainer1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-weight: bold;
  /* cursor: pointer; */
}
.DialogModal__buttonSubContainer2 {
  display: flex;
  /* background-color: green; */
}

.DialogModal__button1 {
  padding: 2px;
  margin-top: 2px;
  font-size: 1.5vh;
  text-align: center;
  border-radius: 15px;
  cursor: pointer;
  user-select: none;
  font-weight: bold;
  /* border-bottom: 1px white; */
  /* background-color: red; */
}
.DialogModal__button2 {
  margin-right: 5px;
  padding: 5px;
  font-size: 2vh;
  text-align: center;
  border-radius: 15px;
  cursor: pointer;
  user-select: none;
  font-weight: bold;
}
